<template>
  <div>
    <div class="search">
      <p>Search</p>
      <el-autocomplete v-model="param"
                       :fetch-suggestions="querySearchAsync"
                       style="width:100%"
                       placeholder="请输入内容"
                       @select="handleSelect">
        <template slot-scope="{ item }">
          <div class="name">{{ item.title }}</div>
          <!-- <span class="addr">{{ item.address }}</span> -->
        </template>
      </el-autocomplete>
    </div>
    <div class="tags">
      <p>Tags</p>
      <el-tag class="tag"
              v-for="(count,tag) in Tags"
              :key="tag">
        <div @click="getArticleListByTag(tag)">
          {{tag}}
          <span class="count">{{count}}</span>
        </div>
      </el-tag>
    </div>
  </div>
</template>

<script>
export default {
  name: 'article-aside',
  props: ['getArticleList'],
  data () {
    return {
      param: '',
      Tags: {}
    }
  },
  mounted () {
    this.axios.getTags()
      .then(res => {
        this.Tags = res.data
      })
      .catch(err => console.error(err))
  },
  methods: {
    getArticleListByTag (tag) {
      this.getArticleList(tag)
    },
    querySearchAsync (queryString, cb) {
      this.axios.search(queryString)
        .then(res => {
          cb(res.data)
        })
        .catch(err => {
          console.error(err)
        })
    },
    handleSelect (item) {
      this.$router.push(`/article/${item.id}`)
    }
  }
}
</script>

<style scoped lang="scss">
.tag {
  margin: 3px;
  cursor: pointer;
}
.tag:hover {
  transform: scale(1.1);
}
</style>



